    <p>
      On May 30, 1948&mdash;on Memorial Day, with everything ready for the
      downtown parade&mdash;residents of the workers' town of Vanport awoke to
      an official handbill slipped beneath the door:
    </p>

    <div class="quote">
      <p>
        REMEMBER<br />DIKES ARE SAFE AT PRESENT<br />YOU WILL BE WARNED IF
        NECESSARY<br />YOU WILL HAVE TIME TO LEAVE<br />DON'T GET EXCITED
      </p>
    </div>

    <p>
      The Portland Housing Authority had built the city to provide lodging for
      over forty thousand laborers who took employment in the nearby Kaiser
      shipyards during World War II. The layout of the streets, buildings and
      plazas in the form of a recumbent lion was undoubtedly meant to evoke
      military power, bolstered by wartime production. The workers themselves
      came from across the state and across the country, drawn by patriotism,
      high wages, modern living, and for some, the prospect of relief from the
      racist violence of the South. There were all kinds of problems, but New
      Deal enthusiasm ran high. For a few short years, Vanport was a utopia.
    </p>

    <p>
      But Portland itself was a
      <a
        href="https://books.google.com/books?id=abhIDwAAQBAJ&lpg=PP1&dq=sundown%20town&pg=PT18#v=onepage&q&f=false"
        target="_blank"
        rel="nofollow"
        >"sundown town"</a
      >
      that informally barred blacks from permanent inhabitation and property
      ownership, just as the state of Oregon had formally excluded black people
      by constitutional law at its foundation in 1859. Institutional racism was
      a heavy reality in the mid-1940s, despite the egalitarian policies of the
      federal government and the good will of fellow migrants, including many of
      Native American, Hispanic or Chinese origins.
    </p>

    <p>
      On that Memorial Day morning in 1948, three years after war's end, the
      population of the vast housing complex had shrunk to about 18,500. Of
      these, some 6,300 were black. Redlining in the city left them with nowhere
      else to go. The lowest point of Vanport was situated fifteen feet beneath
      the normal level of the Columbia River, so the threat to all the remaining
      migrants was obvious. Why didn't the Housing Authority take action?
    </p>

    <p>
      At 4:17 pm the river came knocking. A railroad embankment separating Vanport
      from Smith Lake gave way, as anyone could have predicted it would.
      Floodwaters from the Columbia River surged through the slough and into
      Vanport. The wall of water lifted entire buildings from the ground. In a
      matter of minutes, Oregon's second largest city was entirely destroyed.
      When the water receded the official death toll stood at fifteen, with
      seven more counted missing. But trust in anything official had been
      shattered. To this day, many believe the real toll was much higher.
    </p>

    <img src="images/VanportUnderwater.jpg" />

    <p>
      Vanport&mdash;both the wartime city and the postwar flood&mdash;set the
      contradictory terms of racial coexistence in Portland, and more broadly,
      in the Pacific Northwest. The promise of an egalitarian society, offered
      by an idealistic democracy and violently undercut at street level, still
      characterizes the migrant experience. This is true for many black
      survivors of the flood, who settled in the nearby Albina neighborhood only
      to be driven out by the recent wave of gentrification. How long can the
      new Jim Crow continue?
    </p>

    <p>
      Yet the Vanport Flood has a second significance, also with decisive
      consequences. In the years after 1948, during the negotiation of the
      Columbia River Treaty with Canada, the flood was continually invoked as a
      justification for the construction of a fully integrated system of
      multipurpose dams. Every such dam, whatever its benefits, is also a
      planned disaster. Celilo Falls, along with untold numbers of sites held
      sacred by their Native American inhabitants, would be flooded with the
      same waters that had drowned the workers' metropolis. From Eugene Oregon
      to Vancouver BC, the bright lights of the Cascadian megaregion glitter
      against an historical background of raw coercive power, wielded over the
      destinies of beings both human and non-human.
    </p>

    <p>
      The flood tide of climate change advances, but remember just one thing.
      Dikes are safe at present. You will be warned if necessary. You will have
      time to leave. So don't get excited. Vanport is everywhere. Life under racial capitalism goes on.
    </p>
